<template>
  <v-form ref="form" @submit.prevent>
    <v-container>
      <v-row align="center" justify="center">
        <v-subheader class="text-center">Intenção de Doar Cesta Básica</v-subheader>
      </v-row>
      <v-row align-center hidden>
        <v-col class="d-flex" cols="6">
          <v-text-field v-model="intencao.cdIntencao" label="Id" outlined disabled></v-text-field>
        </v-col>
      </v-row>
      <v-row align="center" justify="center">
        <v-col class="d-flex" cols="6">
          <v-combobox
            :items="doadores"
            :hint="`${'CPF do Doador:' + intencao.doador.cpf}`"
            persistent-hint
            v-model="intencao.doador"
            item-text="nome"
            item-value="cdUsuario"
            label="Doador"
            placeholder="Escolha o Doador"
            return-object
            outlined
            required></v-combobox>
        </v-col>
      </v-row>
      <v-row align="center" justify="center">
        <v-col class="d-flex" cols="6">
          <v-combobox
          :items="instituicoes"
          :hint="`${formatedHint}`"
          persistent-hint
          v-model="intencao.instituicao"
          item-text="nome"
          item-value="cdUsuario"
          label="Instituição"
          placeholder="Escolha a Instituição"
          return-object
          outlined
          required></v-combobox>
        </v-col>
      </v-row>
      <v-row align="center" justify="center">
        <v-col class="d-flex" cols="6">
          <v-combobox
            :items="tpCestas"
            :hint="`${intencao.tpCesta.descricao}`"
            persistent-hint
            v-model="intencao.tpCesta"
            item-text="nome"
            item-value="id"
            label="Tipo de Cesta"
            placeholder="Escolha o Tipo de Cesta"
            return-object
            outlined
            required></v-combobox>
        </v-col>
      </v-row>
      <v-row align="center" justify="center">
        <v-col class="d-flex" cols="6" >
            <v-slider
              v-model="intencao.qtdCestas"
              type="number"
              min=1
              max=50
              :value="`${intencao.qtdCestas}`"
              thumb-label="always"
              label="Qtd. Cestas"
              outlined
              required></v-slider>
          </v-col>
        </v-row>
        <v-row align="center" justify="end">
          <v-col class="d-flex" cols="6" >
            <v-btn color="success" class="" @click="save">Salvar</v-btn>
          </v-col>
        </v-row>
    </v-container>
  </v-form>
</template>

<script>
import DataService from "../service/IntencaoDataService";
import DoadorDataService from "../service/DoadorDataService";
import InstituicaoDataService from "../service/InstituicaoDataService";
import TipoCestaDataService from "../service/TipoCestaDataService";

export default {
  data: () => ({
    doadores: [],
    instituicoes: [],
    tpCestas: [],
    intencao: {cdIntencao:0, dtIntencao:'', qtdCestas:0, status:0, tpCesta:{id:0,nome:'', descricao:''}, doador: {cdUsuario:0,nome:'',login:'',senha:'',cpf:'',rua:'',numCasa:'',bairro:{id:0,nome:'', cidade: {id:0,nome:'',uf:{id:0,sigla:'',nome:''}}}}, beneficiado:{cdUsuario:0}},
  }),
  computed: {
    id() {
      return this.$route.params.id;
    },
    now() {
      return new Date().toISOString().substr(0, 10)
    },
    formatedHint() {
      return this.intencao.doador.bairro.nome + ' - ' + this.intencao.doador.bairro.cidade.nome + ' - ' + this.intencao.doador.bairro.cidade.uf.nome
    }
  },
  watch: {

  },
  methods: {
    initialize() {
      if(this.id > 0){
        DataService.retrieve(this.id).then(response => {
          this.intencao = response.data
        });
      }
      DoadorDataService.retrieveAll().then(response => {
        this.doadores = response.data
      });
      InstituicaoDataService.retrieveAll().then(response => {
        this.instituicoes = response.data
      });
      TipoCestaDataService.retrieveAll().then(response => {
        this.tpCestas = response.data
      });
    },
    save(e) {
      e.preventDefault();
      if(this.id > 0){
        DataService.update(this.id, this.intencao, {
          id: this.id,
          intencao: this.intencao
        }).then(() => {
          this.$router.push('/intencoes')
        }).catch(e => {
          console.log(e)
          if(e.response){
            this.$store.dispatch('setSnackbar', {
              showing: true, text: e.response.data.message, timeout: 8000, color: 'error'
            });
          }
        });
      } else {
        this.intencao.dtIntencao = this.now
        this.intencao.status = 0
        DataService.create(this.intencao, {
          intencao: this.intencao
        }).then(() => {
          this.$router.push('/intencoes')
        }).catch(e => {
          console.log(e)
          if(e.response){
            this.$store.dispatch('setSnackbar', {
              showing: true, text: e.response.data.message, timeout: 8000, color: 'error'
            });
          }
        });
      }
    }
  },
  created() {
    this.initialize()
  }
};
</script>

